.form__container{
    padding: 20px;
    background-color: azure;
}
.input{
    display: block;
    width: 300px;
    height: 30px;
    border: none; 
    outline: 2px solid yellow;
    border-radius: 10px;
    font-size: 16px;
    padding: 5px;
    margin-bottom: 10px; 
}

.label{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 10px;
}
.label__form{
    font-weight: 700; 
    width: 80px;
}
.button_add {
    margin-left: 150px;
 

}
button:hover, button:focus{
    background-color: rgb(233, 233, 113);
}